<template>
	<div class="sz">
		<div class="clock">
			<div class="clock-face">
				<div class="hand second"></div>
				<div class="hand min"></div>
				<div class="hand hour"></div>
				<div class="center"></div>
			</div>
			<div class="sj"></div>
		</div>

	</div>
</template>

<script>
	export default {
		mounted(){
			const second = document.querySelector('.second');
			const min = document.querySelector('.min');
			const hour = document.querySelector('.hour');
			const shijian = document.querySelector('.sj');

			function setDate() {
				let now = new Date();

				const seconds = now.getSeconds();
				const sjisuan = ((seconds / 60) * 360 + 90);
				second.style.transform = `rotate(${sjisuan}deg)`;

				const mins = now.getMinutes();
				const mjisuan = ((mins / 60) * 360 + 90);
				min.style.transform = `rotate(${mjisuan}deg)`;

				const hours = now.getHours();
				const hjisuan =  ((hours / 12) * 360 + 90 + (mins / 12 / 60 ) * 360);
				hour.style.transform = `rotate(${hjisuan}deg)`;

				if (sjisuan === 90) {
					second.style.transition = 'all 0s';
				}else{
					second.style.transition = 'all 0.05s';
				}

 				
				shijian.innerHTML = `北京时间` + hours + `点 ` + mins + `分 ` + seconds + `秒`
			}

			setInterval(setDate,1000);
			setDate()
		}
	}
</script>

<style lang="scss" scoped>
	.sz{
		display: flex;
		justify-content: center;
		.clock{
			width: 4rem;
			height: 4rem;
			border:.14rem solid black;
			border-radius: 50%;
			padding: .14rem;
			box-sizing: border-box;
			.clock-face{
				position: relative;
				width: 100%;
				height: 100%;
				.hand{
					position:absolute;
					width: 50%;
					height: .04rem;
					background: black;
					top: 50%;
					transform-origin: right;
					transform: rotate(90deg);
					transition: .5s;
					transition-timing-function: cubic-bezier(0.1, 2.7, 0.58, 1);
				}
				.second{
					height: .06rem;
					margin-top: -.03rem;
					background:red;
					border-bottom-left-radius: 100%;
        			border-top-left-radius: 100%;
        			z-index: 3;
				}
				.min{
					height: .12rem;
					z-index: 2;
				}
				.hour{
					height: .2rem;
					margin-top: -.1rem;
					z-index: 1;
				}
				.center{
					width: .2rem;
					height: .2rem;
					background:white;
					top: 50%;
					border:.1rem solid black;
					position:absolute;
					margin-top: -.15rem;
					border-radius:50%;
					left: 50%;
					margin-left: -.15rem;
					z-index: 4;
				}
			}
		}
		.sj{
			margin-top: 1rem;
			text-align: center;
			font-size: .3rem;
		}
	}
</style>